import React, { FunctionComponent, AllHTMLAttributes } from 'react';
import xss, { getDefaultWhiteList } from 'xss';
import { specificTagChanges, addToAll } from './CustomWhitelist';

export interface XssSafeContentProps extends AllHTMLAttributes<HTMLElement> {
    content: string;
    elementType?: string | FunctionComponent<{ dangerouslySetInnerHTML: { __html: string } }>;
}

/**
 * Function to render content that has been sanitized by the xss library
 * @param {{content: string, elementType: string, ...props: AllHTMLAttributes<HTMLElement>}} props - The content to be rendered, the element type to render the content inside of and any props to be passed to the div
 * @returns {JSX.Element} - The JSX element to be rendered
 */
function XssSafeContent({ content, elementType = 'div', ...props }: XssSafeContentProps) {
    const customWhitelist = getDefaultWhiteList();

    // Add specific tag changes
    for (const [key, value] of Object.entries(specificTagChanges)) {
        customWhitelist[key] = value;
    }

    // Add additions which all tags get
    Object.keys(customWhitelist).forEach((tag) => customWhitelist[tag]?.push(...addToAll));

    // eslint-disable-next-line react/no-danger
    return React.createElement(elementType, {
        ...props,
        dangerouslySetInnerHTML: { __html: xss(content, { whiteList: customWhitelist }) },
    });
}

export default XssSafeContent;
